.app2 {
    width: 50vw;
    height: 50vh;
    border: 1px solid #000;
}

.switch {
    display: flex;
    width: 100%;
    height: 30px;
}

.selected {
    background: #e5e5e5;
}

.switch>li {
    width: 50%;
    height: 100%;
    border: 1px solid #000;
}

.none {
    display: none;
}